@import url("https://cdn.jsdelivr.net/npm/bootstrap-icons@1.9.1/font/bootstrap-icons.css");
* {
  margin: 0;
  padding: 0;
}
body {
  background-color: rgb(1, 17, 32);
}
.outermostdiv {
  display: flex;
  align-items: center;
  margin: auto;
  margin-top: 5%;
  margin-bottom: 5%;
  flex-direction: column;
  width: 80%;
  border-radius: 5px;
  box-shadow: 1px 1px 10px 2px whitesmoke;
  padding-bottom: 5vh;
}
.image h2 {
  font-family: "Robin";
  color: beige;
  margin: 1vh 0;
  text-align: center;
}
.Additem {
  display: flex;
  justify-content: center;
  align-items: center;
}
#plusLogo {
  color: brown;
  font-size: 50px;
}
#plusLogo:hover {
  cursor: pointer;
}
#textarea {
  width: 30vw;
  height: 7vh;
  border-radius: 3px;
  border: none;
  margin-left: 5vh;
  padding-left: 1vw;
}
h3 {
  color: bisque;
  font-family: "Robin";
}
h4 {
  margin: 1%;
  color: rgb(188, 243, 231);
  font-family: "Robin";
  font-size: 35px;
}
.listedItem {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 40vw;
  background-color: rgb(97, 119, 112);
  margin-right: 2vh;
  border-radius: 2px;
  margin-bottom: 5vh;
}
.content {
  width: 63vh;
  color: rgb(169, 243, 169);
  font-family: "Robin";
  font-size: 4vh;
  padding-left: 1vh;
  margin-left: 2vh;
}
.content:focus {
  outline: none;
}
.edit-deleteLogo {
  font-size: 37px;
  display: flex;
}

.edit-deleteLogo:hover {
  cursor: pointer;
}

.allClear {
  background-color: rgb(250, 168, 168);
  font-family: "Rubika";
  width: 15%;
  font-size: 25px;
  border-radius: 5px;
}
.allClear:hover {
  background-color: red;
}
